
import { defineComponent } from 'vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { APM20600112_REQ, APM20600112_RES } from '@/shared/type/APM20600112.type';
import { modalController } from '@ionic/vue'
import { APM20600121_REQ, APM20600121_RES } from '@/shared/type/APM20600121.type';
import { APM20600131_REQ, APM20600131_RES } from '@/shared/type/APM20600131.type';
import { APM20100113_BODY, APM20100113_RES, } from "@/shared/type/APM20100113.type";
import type { COM20600111_RES, COM20600111_REQ } from "@/shared/type/COM20600111.type";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue"
export default defineComponent({
    name: "apm205001sc01",
    components: {
        BreadCrumb,
        SelectOption
    },
    data() {
        const todoAuthorization = {
            serviceID: "",
            authorizationBizTypeCode1: "",
            authorizationBizTypeCode2: "",
            statusCode: "",
            serviceIDDescription: "",
            serviceIDName: ""
        }
        return {
            todoAuthorization,
            isDisabledEdit: true as boolean,
            isInValidInput: false as boolean,
            isDisabledRegisterMode: false as boolean,
            AuthorizationBizTypeCode2: [] as any,
            tempServiceID: [] as any,
            invalidServiceID: false as boolean,
            invalidLength: false as boolean,

        }
    },
    async mounted() {
        if (this.queryParam.type == 'edit') {
            await this.inquiryAuthorizationCode2();
            await this.getTodoAuthorizedDetail();
            this.isDisabledEdit = true;
        } else {
            this.isDisabledEdit = false;
            this.todoAuthorization.statusCode = this.statusCode[0].key;
            this.isDisabledRegisterMode = true;
        }

    },
    computed: {
        queryParam() {
            return this.$route.query
        },
        statusCode() {
            return this.$store.state.CommonCodeModule.commonCode.StatusCode
        },
    },
    watch: {
        "todoAuthorization.authorizationBizTypeCode1": {
            handler(value) {
                if (value) {
                    this.inquiryAuthorizationCode2(value);
                }
            }
        },
        "todoAuthorization.serviceID": {
            handler(value) {
                if (value == '') {
                    this.invalidServiceID = false;
                    this.todoAuthorization.serviceIDName = '';
                    this.todoAuthorization.serviceIDDescription = ''
                    this.invalidLength = false;
                }
            }
        },
    },
    methods: {
        async getTodoAuthorizedDetail() {
            const resquestBody: APM20600112_REQ = {
                serviceID: String(this.queryParam.serviceID),
            }
            const responseBody = await this.$request.getRequest('APM20600112', resquestBody, false) as APM20600112_RES;
            if (responseBody.header.result) {
                this.todoAuthorization = responseBody.body;
            }
        },

        handleEnableEditButton() {
            this.isDisabledEdit = false;
        },

        handleCancelButton() {
            let content = '';
            let dialogMode = '' as "Question" | "Success" | "Error" | "Warning" | "Info" | undefined
            if (this.queryParam.type === 'register') {
                dialogMode = "Question";
                content = this.$t('message.move_to_list_without_saving_changes')
            } else {
                dialogMode = 'Question'
                content = this.$t('message.cancel_without_saving_changes')
            }
            this.$dialog.onOpen({
                dialogMode,
                content,
                callback: () => this.cancelEvent()
            })
        },

        async cancelEvent() {
            modalController.dismiss()
            if (this.queryParam.type === 'register') {
                this.$router.push({ path: '/APM205001SC00' })
            } else {
                this.isDisabledEdit = true;
                await this.getTodoAuthorizedDetail();
                await this.inquiryAuthorizationCode2();
            }
        },

        goBackToInquiryScreen() {
            this.$router.push({ path: '/APM205001SC00' })
        },

        checkInValidInput(): boolean {
            const { serviceID, authorizationBizTypeCode1, authorizationBizTypeCode2, statusCode } = this.todoAuthorization;
            if (serviceID === "" || authorizationBizTypeCode1 === "" || authorizationBizTypeCode2 === "" || statusCode === "") {
                this.isInValidInput = true
                return true
            } else {
                this.isInValidInput = false
                return false
            }
        },

        handleSaveEvent() {
            if (this.checkInValidInput()) {
                this.isInValidInput = true;
            } else {
                this.$dialog.onOpen({
                    dialogMode: "Question",
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => {
                        modalController.dismiss()
                        if (this.queryParam.type === 'register') {
                            this.registerTodoAuthorization()
                        } else {
                            this.updateTodoAuthorization();
                        }
                    }
                })
            }
        },

        async registerTodoAuthorization() {
            const requestBody: APM20600121_REQ = {
                serviceID: this.todoAuthorization.serviceID,
                authorizationBizTypeCode1: this.todoAuthorization.authorizationBizTypeCode1,
                authorizationBizTypeCode2: this.todoAuthorization.authorizationBizTypeCode2,
                statusCode: this.todoAuthorization.statusCode,
            }

            const responseBody = await this.$request.getRequest('APM20600121', requestBody, false) as APM20600121_RES;
            if (responseBody.header.result) {
                this.$router.push({ path: '/APM205001SC00' })
                this.$dialog.onOpen({
                    dialogMode: "Success",
                    content: "Register Todo Authorization Relation Service Success",
                    callback: () => modalController.dismiss()
                })
                this.isDisabledEdit = true;
            }
        },

        async updateTodoAuthorization() {
            const requestBody: APM20600131_REQ = {
                serviceID: this.todoAuthorization.serviceID,
                authorizationBizTypeCode1: this.todoAuthorization.authorizationBizTypeCode1,
                authorizationBizTypeCode2: this.todoAuthorization.authorizationBizTypeCode2,
                statusCode: this.todoAuthorization.statusCode,
            }

            const responseBody = await this.$request.getRequest('APM20600131', requestBody, false) as APM20600131_RES;
            if (responseBody.header.result) {
                this.isDisabledEdit = true;
                this.$dialog.onOpen({
                    dialogMode: "Success",
                    content: "Update Todo Authorization Relation Service Success",
                    callback: () => modalController.dismiss()
                })
                this.getTodoAuthorizedDetail();
            }
        },

        async inquiryAuthorizationCode2(value?) {
            const requestBody: APM20100113_BODY = {
                authorizationBizTypeCode1: value,
            };

            const responseBody = await this.$request.getRequest("APM20100113", requestBody, false) as APM20100113_RES;
            if (responseBody.header.result) {
                this.AuthorizationBizTypeCode2 = responseBody.body.list.map((item) => ({
                    key: item.authorizationBizTypeCode2,
                    value: item.managerApprovalDescription
                }))
            }
        },

        async requestServiceDetail(userClick: boolean) {
            this.todoAuthorization.serviceIDName = '';
            this.todoAuthorization.serviceIDDescription = ''
            if (this.todoAuthorization.serviceID.length >= 11) {
                this.invalidLength = false;
                const reqBody = {
                    serviceIDName: '',
                    serviceID: this.todoAuthorization.serviceID.trim(),
                    pageSize: 10000,
                    pageNumber: 1,
                    checkServiceYN: '',
                    transactionControlStatusCode: ''
                } as COM20600111_REQ;
                const res = await this.$request.getRequest('COM20600111', reqBody, false) as COM20600111_RES;
                if (res.header.result) {
                    this.invalidServiceID = userClick;
                    this.invalidServiceID ? this.tempServiceID = res.body.list : '';
                    res.body.list.find((item) => {
                        if (item.serviceID.trim() == this.todoAuthorization.serviceID.trim()) {
                            this.todoAuthorization.serviceIDName = item.serviceID;
                            this.todoAuthorization.serviceIDDescription = item.serviceIDDescription;
                        }
                    })

                }
            } else {
                this.invalidLength = true;
                this.invalidServiceID = false;
            }

        },
    }

})
